@charset "UTF-8";
.pageWrap {
  margin-top: 80px; }

main > aside.homeAside1:first-of-type {
  width: 15vw;
  position: fixed; }

main > aside.homeAside2:last-of-type {
  margin-left: 15vw; }

.titleWrap header {
  color: #505050; }

.homeColorFilter {
  justify-content: space-between;
  flex-wrap: wrap; }
  .homeColorFilter li {
    opacity: 0.7;
    padding: 20px;
    width: 40%; }

.productList {
  justify-content: space-between;
  display: flex;
  flex-wrap: wrap; }
  .productList .productWrap {
    padding: 0px 8px;
    margin-bottom: 80px;
    width: 26%;
    height: 450px; }
    .productList .productWrap:hover {
      outline: 1px solid lightgrey; }
    .productList .productWrap a {
      text-decoration: none; }
      .productList .productWrap a img {
        width: 100%;
        height: 350px; }

.productDescription div {
  margin-bottom: 10px;
  font-weight: bold; }

.productDescription div:first-of-type {
  border-bottom: 1px solid #e2e2e2; }

.productDescription div:last-of-type {
  color: #003354;
  font-weight: normal; }

h1 {
  font-family: Arvo,Arial,Helvetica,sans-serif;
  font-weight: 400;
  font-size: 1.875rem;
  line-height: 2.75rem;
  letter-spacing: .01rem;
  color: #2D2D2D; }

.mobileFilter {
  display: none; }

/*tablet design*/
@media screen and (max-width: 790px) {
  .mobileFilter {
    display: block; }
  main > aside.homeAside1:first-of-type {
    display: none; }
  main > aside.homeAside2:last-of-type {
    margin-left: 0px; }
  .productList .productWrap {
    width: 30%; } }

@media screen and (max-width: 420px) {
  .productList .productWrap {
    width: 45%; } }

.select {
  display: inline-block;
  cursor: pointer; }

.color {
  display: block;
  /*   padding: 5px 5px; */
  border: 1px solid white; }
  .color:hover {
    border-color: #D3D3D3; }

input {
  /* 利用透明度讓選取的點看不到 */ }

input:checked + .color {
  border-color: #D3D3D3;
  /* 當選項被點選時 checked 就改變 */ }

input:disabled + .color {
  background: #eee;
  color: #999;
  /* 在 input 後加上 disabled 讓按鍵出現但沒功能 */ }

.fuck1 {
  opacity: 0.7;
  width: 25px;
  height: 25px;
  margin: 5px; }

#fuck2 {
  width: 13px;
  height: 13px;
  margin: 5px; }
