.Input {
    width: 100%;
    // padding: 8px 0px;
    box-sizing: border-box;
    border-radius:5px;
}

.Label {
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
}

.InputElement {
    font-size:18px;
    border: 0;
    padding-left: 20px;
    height: 67px;
    width: 424px;
    margin-bottom: 15px;
    background-color: #f2f2f2;
    letter-spacing: 2.5px;
    color: black;
}

.InputElement:focus {
    outline: none;
    background-color: #ccc;
}

.Invalid {
    border: 1px solid red;
    background-color: #FDA49A;
}

